<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <!-- form -->
      <el-form ref="resetPassFormRef"
        :model="resetPassForm"
        :rules="RestPassFormRules"
        label-width="100px"
        class='login_form'>
        <!--  -->
        <el-form-item prop="password" label="" :required="true">
          <!-- <el-input prefix-icon="el-icon-unlockss"></el-input> -->
          <el-input v-model="resetPassForm.password"
                    prefix-icon="el-icon-lock"
                    placeholder=""
                    type='password'>
          </el-input>
        </el-form-item>
        <!--  -->
        <el-form-item prop="newpassword" label="" :required="true">
          <el-input v-model="resetPassForm.newpassword"
                    prefix-icon="el-icon-lock"
                    placeholder=""
                    type='password'>
          </el-input>
        </el-form-item>
        <!--  -->
        <el-form-item prop="checknewpassword" label="" :required="true">
          <el-input v-model="resetPassForm.checknewpassword"
                    prefix-icon="el-icon-lock"
                    placeholder=""
                    type='password'>
          </el-input>
        </el-form-item>
        <!--  -->
        <el-form-item label="" class='btn-s'>
          <el-button type='primary' @click='passConfirm'></el-button>
          <el-button type='info' @click='resetPassFormMethod'></el-button>
        </el-form-item>
      </el-form>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      v-model="addDialogVisible"
      width="30%"
      :visible="addDialogVisible">
      <span></span>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="addDialogVisible = false"> </el-button>
          <el-button type="primary" @click="addDialogVisible = false"> </el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
      } else if (value !== this.resetPassForm.newpassword) {
        callback(new Error('!'))
      } else {
        callback()
      }
    }
    return {
      resetPassForm: {
        password: '',
        newpassword: '',
        checknewpassword: ''
      },
      // 
      RestPassFormRules: {
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 4, max: 15, message: ' 4  15 ', trigger: 'blur' }
        ],
        newpassword: [
          { required: true, message: '', trigger: 'blur' },
          { min: 4, max: 15, message: ' 4  15 ', trigger: 'blur' }
        ],
        checknewpassword: [
          { validator: validatePass2, trigger: 'blur' },
          { min: 4, max: 15, message: ' 4  15 ', trigger: 'blur' }
        ]
      },
      addDialogVisible: false
    }
  },
  /* created () {
    this.getUserList()
  }, */
  methods: {
    // 
    resetPassFormMethod () {
      this.$refs.resetPassFormRef.resetFields()
    },
    passConfirm () {
      this.$refs.resetPassFormRef.validate((valid) => {
        if (valid) {
          this.$confirm('?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(async _ => {
            const postParams = {
              password: this.resetPassForm.password,
              newpassword: this.resetPassForm.newpassword
            }
            const { data: res } = await this.$http.post('reset_password', postParams)
            if (res.meta.status !== 200) {
              return this.$message.error(res.meta.msg)
            } else {
              this.$router.push('/home')
              return this.$message.success('')
            }
          }).catch(_ => {})
        } else {
          this.$message.error('!')
        }
      })
    }
  }
}
</script>
<style>
  /* .btn-s {
    display: flex;
    justify-content: flex-end;
  } */
  .login_form {
    width: 50%;
    padding: 0 20px;
    box-sizing: border-box;
  }
</style>
