<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="10">
          <el-input placeholder=""
          v-model="queryInfo.query"
          :clearable="true"
          @clear="handleClear">
            <el-select v-model="searchOptionValue"
              slot="prepend"
              placeholder=""
              class="card-input-select"
              @change='changeQueryTag'>
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
            <template #append>
              <el-button icon="el-icon-search"
              @click="handleSearch">
              </el-button>
            </template>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary"
          @click="addDialogVisible = true">
          
          </el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="scriptlist" :border="true" :stripe="true">
        <el-table-column label="#" type="index"></el-table-column>
        <el-table-column label="" prop="label"></el-table-column>
        <!-- <el-table-column label="" prop="command"></el-table-column> -->
        <el-table-column label="" width="120px">
          <!-- <template v-slot="scope"> -->
          <template v-slot="scope">
            <!--  -->
            <el-button
              type="primary"
              icon="el-icon-edit"
              @click="handleModApp(scope.row)"
              size="mini">
            </el-button>
            <!--  -->
            <el-button
              v-model="scope.row.app_id"
              @click="handleDeleteApp(scope.row)"
              type="danger"
              icon="el-icon-delete"
              size="mini">
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="queryInfo.page_index"
            :page-sizes="[5, 10, 20, 50]"
            :page-size="queryInfo.page_size"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
          </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      v-model="addDialogVisible"
      width="50%"
      :show-close="false"
      :visible="addDialogVisible">
      <el-form :model="addform">
          <el-form-item label=" : " :label-width="formLabelWidth">
            <el-input v-model="addform.label" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label=" : " :label-width="formLabelWidth">
            <el-input v-model="addform.command"  type="textarea" :rows="11" autocomplete="off"></el-input>
          </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="handleClose"> </el-button>
          <el-button type="primary" @click="handleSubmit"> </el-button>
        </span>
      </template>
    </el-dialog>
    <!-- dialog -->
    <el-dialog
      title=""
      v-model="modDialogVisible"
      width="50%"
      :show-close="false"
      :visible="modDialogVisible">
      <el-form :model="modform">
          <el-form-item label=" : " :label-width="formLabelWidth">
            <el-input v-model="modform.label" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label=" : " :label-width="formLabelWidth">
            <el-input v-model="modform.command"  type="textarea" :rows="11" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="handleModClose"> </el-button>
          <el-button type="primary" @click="handleModSubmit"> </el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 
      options: [{
        value: '1',
        label: ''
      }, {
        value: '2',
        label: ''
      }],
      searchOptionValue: '',
      // 
      scriptTempOptions: [],
      scriptTempOptionValue: '',
      // 
      queryInfo: {
        query: '',
        // tag 0 , 1 , 2 , 3 
        query_tag: 0,
        // 
        page_index: 1,
        // 
        page_size: 10
      },
      scriptlist: [],
      total: 0,
      // dialog
      addDialogVisible: false,
      // dialog
      addform: {
        label: '',
        command: ''
      },
      modform: {
        id: '',
        label: '',
        command: ''
      },
      modDialogVisible: false,
      formLabelWidth: '120px'
    }
  },
  created () {
    this.getScriptList()
  },
  methods: {
    logout () {
      window.sessionStorage.clear()
      this.$router.push('/login')
    },
    async getScriptList () {
      try {
        const { data: res } = await this.$http.get('query_script_temp', { params: this.queryInfo })
        if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
        this.scriptlist = res.data.app_list
        this.total = res.data.total_rows
        // this.page_index = res.data.page_index
        this.queryInfo.page_index = res.data.page_index
      } catch (e) {
        const erroInfo = e.toString()
        if (erroInfo.indexOf('status code 401') !== -1) {
          console.log(',')
          this.logout()
          return this.$message.error(',')
        }
        return this.$message.error(erroInfo)
      }
    },
    // 
    handleClear () {
      this.queryInfo.page_index = 1
      this.getScriptList()
    },
    // 
    handleSearch () {
      if (this.queryInfo.query_tag !== 2) {
        this.queryInfo.query_tag = 1
      } else {
        this.changeQueryTag()
      }
      this.getScriptList()
    },
    // pageszie 
    handleSizeChange (newSize) {
      // console.log(newSize)
      this.queryInfo.page_size = newSize
      this.getScriptList()
    },
    // 
    handleCurrentChange (newPage) {
      this.queryInfo.page_index = newPage
      this.getScriptList()
    },
    // dialog
    handleClose () {
      this.addform.label = ''
      this.addform.command = ''
      this.addDialogVisible = false
    },
    // dialog
    handleModClose () {
      this.modDialogVisible = false
      this.modform.id = -1
      this.modform.label = ''
      this.modform.command = ''
    },
    // appdialog
    handleModApp (scriptinfo) {
      this.modDialogVisible = true
      this.modform.id = scriptinfo.id
      this.modform.label = scriptinfo.label
      this.modform.command = scriptinfo.command
    },
    // id
    handleDeleteApp (scriptinfo) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async _ => {
        console.log(scriptinfo.id)
        this.DeleteId = scriptinfo.id
        const { data: res } = await this.$http.delete('delete_script_temp', { params: { id: this.DeleteId } })
        console.log(res)
        if (res.meta.status !== 200) {
          return this.$message.error(res.meta.msg)
        } else {
          this.getScriptList()
          return this.$message.success('')
        }
      }).catch(_ => {})
    },
    // 
    async handleSubmit () {
      const { data: res } = await this.$http.post('add_script_temp', this.addform)
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      } else {
        this.addform.label = ''
        this.addform.command = ''
        this.queryInfo.query_tag = 3
        this.getScriptList()
        this.queryInfo.query_tag = 0
        return this.$message.success('')
      }
    },
    async handleModSubmit () {
      const { data: res } = await this.$http.put('mod_script_temp', this.modform)
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      } else {
        this.handleModClose()
        this.getScriptList()
        return this.$message.success('')
      }
    },
    changeQueryTag () {
      this.queryInfo.query_tag = this.searchOptionValue
    }
  }
}
</script>

<style Lang="less" scoped>
  .card-input-select {
    width: 90px;
  }
</style>
