<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card class="box-card">
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="10">
          <el-input placeholder=""
          v-model="queryInfo.query"
          :clearable="true"
          @clear="handleClear">
            <el-select v-model="searchOptionValue"
              slot="prepend"
              placeholder=""
              class="card-input-select"
              @change='changeQueryTag'>
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
            <template #append>
              <el-button icon="el-icon-search"
              @click="handleSearch">
              </el-button>
            </template>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary"
          @click="addDialogVisible = true">
          
          </el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="applist" :border="true" :stripe="true">
        <el-table-column label="#" type="index"></el-table-column>
        <el-table-column label="ip" prop="ip"></el-table-column>
        <el-table-column label="cluster_info" prop="cluster_info"></el-table-column>
        <el-table-column label="" width="120px">
          <template v-slot="scope">
            <!--  -->
            <el-button
              type="primary"
              icon="el-icon-edit"
              @click="handleModApp(scope.row)"
              size="mini">
            </el-button>
            <!--  -->
            <el-button
              v-model="scope.row.app_id"
              @click="handleDeleteApp(scope.row)"
              type="danger"
              icon="el-icon-delete"
              size="mini">
            </el-button>
            <!--  -->
            <!-- <el-tooltip effect="dark" content="" placement="top">
              <el-button type="warning" icon="el-icon-setting" size="mini"></el-button>
            </el-tooltip> -->
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="queryInfo.page_index"
            :page-sizes="[5, 10, 20, 50]"
            :page-size="queryInfo.page_size"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total">
          </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      v-model="addDialogVisible"
      width="50%"
      :show-close="false"
      :visible="addDialogVisible">
      <el-form :model="form">
          <el-form-item label="ip : " :label-width="formLabelWidth">
            <el-input v-model="form.ip" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label=" : " :label-width="formLabelWidth">
            <el-input v-model="form.cluster_info" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
      <template #footer>
        <span class="dialog-footer">
          <!-- <el-button @click="addDialogVisible = false"> </el-button> -->
          <el-button @click="handleClose"> </el-button>
          <el-button type="primary" @click="handleSubmit"> </el-button>
        </span>
      </template>
    </el-dialog>
    <!-- dialog -->
    <el-dialog
      title=""
      v-model="modDialogVisible"
      width="50%"
      :show-close="false"
      :visible="modDialogVisible">
      <el-form :model="form">
          <el-form-item label="ip : " :label-width="formLabelWidth">
            <el-input v-model="modform.ip" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label=" : " :label-width="formLabelWidth">
            <el-input v-model="modform.cluster_info" autocomplete="off"></el-input>
          </el-form-item>
        </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="handleModClose"> </el-button>
          <el-button type="primary" @click="handleModSubmit"> </el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 
      options: [{
        value: '1',
        label: ''
      }, {
        value: '2',
        label: ''
      }],
      searchOptionValue: '',
      // 
      queryInfo: {
        query: '',
        // tag 0 , 1 , 2 , 3 
        query_tag: 0,
        // 
        page_index: 1,
        // 
        page_size: 10
      },
      applist: [],
      total: 0,
      // dialog
      addDialogVisible: false,
      // dialog
      modDialogVisible: false,
      form: {
        ip: '',
        cluster_info: '',
        app_id: ''
      },
      modform: {
        ip: '',
        cluster_info: ''
      },
      formLabelWidth: '120px'
    }
  },
  created () {
    this.getAppList()
  },
  methods: {
    logout () {
      window.sessionStorage.clear()
      this.$router.push('/login')
    },
    async getAppList () {
      try {
        const { data: res } = await this.$http.get('query_app', { params: this.queryInfo })
        // const { data: res } = await this.$http.post('query_app', this.queryInfo)
        // console.log(res)
        if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
        this.applist = res.data.app_list
        this.total = res.data.total_rows
        // this.page_index = res.data.page_index
        this.queryInfo.page_index = res.data.page_index
        console.log(res)
        console.log(this.applist)
      } catch (e) {
        this.$message.error(e.toString())
        console.log(e)
        const erroInfo = e.toString()
        if (erroInfo.indexOf('status code 401') !== -1) {
          console.log(',')
          this.logout()
          return this.$message.error(',')
        }
        return this.$message.error(erroInfo)
      }
    },
    // 
    handleClear () {
      this.queryInfo.page_index = 1
      this.getAppList()
    },
    // 
    handleSearch () {
      if (this.queryInfo.query_tag !== 2) {
        this.queryInfo.query_tag = 1
      } else {
        this.changeQueryTag()
      }
      this.getAppList()
    },
    // pageszie 
    handleSizeChange (newSize) {
      // console.log(newSize)
      this.queryInfo.page_size = newSize
      this.getAppList()
    },
    // 
    handleCurrentChange (newPage) {
      // console.log(newPage)
      this.queryInfo.page_index = newPage
      this.getAppList()
    },
    // dialog
    handleClose () {
      this.addDialogVisible = false
      this.modform.ip = ''
      this.modform.cluster_info = ''
    },
    // dialog
    handleModClose () {
      this.modDialogVisible = false
      this.modform.ip = ''
      this.modform.cluster_info = ''
      this.modform.app_id = -1
    },
    // appdialog
    handleModApp (appinfo) {
      console.log(appinfo)
      this.modDialogVisible = true
      this.modform.ip = appinfo.ip
      this.modform.cluster_info = appinfo.cluster_info
      this.modform.app_id = appinfo.app_id
    },
    // appid
    handleDeleteApp (appinfo) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async _ => {
        console.log(appinfo.app_id)
        this.DeleteInfo = appinfo.app_id
        const { data: res } = await this.$http.delete('delete_app', { params: { app_id: this.DeleteInfo } })
        console.log(res)
        if (res.meta.status !== 200) {
          return this.$message.error(res.meta.msg)
        } else {
          this.getAppList()
          return this.$message.success('')
        }
      }).catch(_ => {})
    },
    // 
    async handleSubmit () {
      const { data: res } = await this.$http.post('add_app', this.form)
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      } else {
        this.form.ip = ''
        this.form.cluster_info = ''
        this.queryInfo.query_tag = 3
        this.getAppList()
        this.queryInfo.query_tag = 0
        return this.$message.success('')
      }
    },
    async handleModSubmit () {
      const { data: res } = await this.$http.put('mod_app', this.modform)
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      } else {
        this.handleModClose()
        this.getAppList()
        return this.$message.success('')
      }
    },
    changeQueryTag () {
      this.queryInfo.query_tag = this.searchOptionValue
      console.log(this.queryInfo.query_tag)
    }
  }
}
</script>

<style Lang="less" scoped>
  .card-input-select {
    width: 90px;
  }
</style>
